@import "../../core/variables";
.form__btnReset {
  height: 20px;
  padding: 5px;
  border-radius: 5px;
  letter-spacing: 1px;
  background-color: rgba(58, 5, 5, 0.822);
  box-shadow: 4px 4px 5px black;
  color: $mainFontColor;
  font-size: 10px;
  cursor: pointer;
  @media (min-width: 768px) {
    height: 50px;
    padding: 5px;
    font-size: 16px;
  }

  &:hover {
    box-shadow: 5px 5px 7px rgb(224, 188, 23);
  }
}
